import React, { Fragment, useRef, useState, useEffect } from "react";
import {
    SectionProjects, ContainerProjects,
    TitleProjects, Description, ContainerDescription,
    IMGContainer, Title
} from "../styles/ProjectsPageStyles"
import PokeGame from "../img/PokeGame.svg"
import {fadeInContainerWithStagger,
    fadeInDown,
    fadeinLeft} from "../styles/Animations.js"
import {useInViewport} from "./useInViewport.jsx"
const ProjectsPage = () => {

    // Detectar si intersectan los objetos y animarlos
    const outerRef = useRef(null);
    const inViewport = useInViewport(outerRef)
    return (
        <Fragment>
                <SectionProjects ref={outerRef}>
                {inViewport &&
                    <ContainerProjects
                    variants={fadeInContainerWithStagger}
                    initial="hidden"
                    animate="visible" >
                         <TitleProjects
                          variants={fadeInDown}>
                            Projects
                         </TitleProjects>
                            <Description>
                                <ContainerDescription
                                initial={{opacity:0, x:"-100px"}}
                                animate={{opacity:1,x:"0"}}
                                transition={{duration:1,delay:1 ,type: "tween", ease: "easeIn"}}>
                                    <Title>
                                        <span>0.1</span>
                                        <h3>Memory Game</h3>
                                    </Title>
                                    <p>
                                       Application that consumes the pokemon api developed in react, and designed in adobe XD
                            </p>
                            <h5>Tap in the image to view more</h5>
                                </ContainerDescription>
                                <IMGContainer
                                variants={fadeInDown}
                                whileTap={{scale:0.9}} >
                                    <img src={PokeGame}></img>
                                </IMGContainer>
                            </Description>
                </ContainerProjects>
            } 
                </SectionProjects>
        </Fragment>
    )

}
export default ProjectsPage