import React, {Fragment, useState, useEffect} from "react";
import HomePage from "./HomePage.jsx"
import ProjectsPage from "./ProjectsPage.jsx";
import AboutMe from "./AboutMe.jsx"
const PageContain = () => {
    // detectar width de la pantalla
    const [widthPC, setwidthPC] = useState(window.innerWidth > 1200 ? true : false);
    const [variablewidth, setvariablewidth] = useState(window.innerWidth)
    const handlewidth = () => {
        setvariablewidth(window.innerWidth)
        console.log("hola")
        variablewidth > 1200 ? setwidthPC(true) : setwidthPC(false)
    };
    useEffect(() => {
        window.addEventListener("resize",handlewidth)
        console.log(variablewidth)
        return () => {
            window.removeEventListener("resize",handlewidth)
        }
    })
    return(
        <Fragment>
            <HomePage widthPC={widthPC}/>
            <ProjectsPage/>
            <AboutMe/>
        </Fragment>
    )
}
export default PageContain;