import {Colors} from "../styles/Colors.js";
import styled from "styled-components";
import {motion} from "framer-motion";
import MenuClosed from "../img/MenuClosed.png"
import MenuOpen from "../img/MenuOpen.png";


export const NavigationBar = styled(motion.nav)`
    position:fixed;
    width:90%;
    height:10%;
    display:flex;
    align-items:center;
    justify-content:space-between;
    margin:0 auto;
    z-index:30;
    top:0;
    @media (min-width:1200px){
        width:90%;
        height:10%;
        top:5%;
    }
`;
export const Logo = styled.h2`
    font-size:30px;
    font-family:"MS Gothic";
    color:${Colors.title};
    letter-spacing:3px;
    position:relative;
    margin:0 5%;
    z-index:30;
    @media (min-width:768px){
        font-size:45px;
        margin: 0 5%;
    }
    @media (min-width:1200px){
        font-size:40px;
    }
`;
export const  MenuHamborguer = styled.div`
    width:15%;
    height:30%;
    background-image: url(${props => (props.clickMenu) ? MenuClosed : MenuOpen});
    background-repeat:no-repeat;
    background-size:contain;
    z-index:50;
    @media (min-width:768px){
        position: relative;
        left:8%;
        height:30%;
    }

`;
