  // Framer motion animations
 export const fadeInContainerWithStagger = {
    hidden: {
        opacity: 1
    },
    visible: {
        opacity: 1,
        transition: {
            duration: .5,
            type: "tween",
            ease: "easeIn",
            when: "beforeChildren",
            staggerChildren: 1.5
        }
    }
};
export const fadeInDown = {
    hidden: {
        opacity: 0,
        y: 40
    },
    visible: {
        opacity: 1,
        y: 0,
        transition: {
            type: "spring",
        }
    }
};
export const fadeinLeft = {
    hidden: {
        opacity: 0,
        x: 40
    },
    visible: {
        opacity: 1,
        x: 0,
        transition: {
            type: "spring",

        }
    }
};