import React,{useRef, Fragment} from "react";
import LandingLuna from "../img/LandingLuna.svg"
import {useInViewport} from "./useInViewport";
import {
    SectionContainer, ImgLanding, ContainerText
    , Text1, Text2, Title1, Title2, ButtonContact,
    MainText
} from "../styles/DataContainerStyles.js"
const DataContainer = () => {
    const Sectionref = useRef();
    const inViewport = useInViewport(Sectionref);
    return (
        <SectionContainer ref={Sectionref}>
            {inViewport &&
        <Fragment>
            <ImgLanding src={LandingLuna}
                initial={{ y: "-300px", opacity: 0 }}
                animate={{ y: "0", opacity: 1 }}
                transition={{ duration: 3, type: "spring", bounce: 0.5 }} />
            <ContainerText>
                <Text1
                    initial={{ opacity: 0 }}
                    animate={{ opacity: 1 }}
                    transition={{ duration: 2, delay: 1 }}>
                    Hi my name is
                </Text1>
                <MainText>
                    <Title1
                    initial={{opacity:0 , x:"-150px"}}
                    animate={{opacity:1, x:"0"}}
                    transition={{duration:2,type:"spring", delay:1.2}}>
                        Carlos
                    </Title1>
                    <Title2
                     initial={{opacity:0 , x:"150px"}}
                     animate={{opacity:1, x:"0"}}
                     transition={{duration:2,type:"spring", delay:1.2}}>
                        Jarero
                    </Title2>
                </MainText>
                <Text2
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{ duration: 2, delay: 1.5 }}>
                    Front-end developer
                </Text2>
                <ButtonContact
            initial={{opacity:0 , x:"-150px"}}
            animate={{opacity:1 , x:"0" }}
            transition={{ duration:3 , delay:1 , type:"spring"}}
            >
                <p>Contact me</p>
            </ButtonContact>
            </ContainerText>
        </Fragment>
            }
        </SectionContainer>
    )
}
export default DataContainer;