import styled from "styled-components";
import {Colors} from "../styles/Colors.js";
import {motion} from "framer-motion";
import Esquina from "../img/Esquina.svg"
export const SectionContainer = styled.section`
    width:90%;
    height:90%;
    margin: 0 auto;
    @media (min-width:1200px){
        display: flex;
        width:100%;
        align-items: center;
        justify-content: space-between;
        &:before{
            content:url(${Esquina});
            position:fixed;
            width:107px;
            height:107px;
            top:2%;
            left:2%;
        }
        &:after{
            content:url(${Esquina});
            position:fixed;
            width:107px;
            height:107px;
            top:2%;
            right:2%;
            transform:rotateY(180deg)
        }
    }
`;
export const ImgLanding = styled(motion.img)`
width:100%;
height:60%;
@media (min-width:768px){
    position:relative;
    top:5%;
}
@media (min-width:1200px){
    height:95%;
    order:2;
    top:5%;
    right:5%;
}
`;
export const ContainerText = styled.div`
    width:100%;
    height:35%;
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:space-between;
    @media (min-width:1200px){
        height:50%;
        width:100%;
        margin-top:5%;
        align-items:flex-start;
        position:relative;
        left:10%;
        &:before{
            content:url(${Esquina});
            position:fixed;
            width:107px;
            height:107px;
            bottom:2%;
            left:2%;
            transform:rotateX(180deg);
        }
        &:after{
            content:url(${Esquina});
            position:fixed;
            width:107px;
            height:107px;
            bottom:2%;
            right:2%;
            transform:rotateZ(180deg);
        }
    }
`;
export const Text1 = styled(motion.p)`
    font-family:"TMC";
    font-size:25px;
    color:${Colors.letters};
    @media (min-width:768px){
        font-size:35px;
    }
    @media (min-width:1200px){
        position:relative;
        text-align:justify;
    }
`;
export const Text2 = styled(Text1)`

`;
export const Title1 = styled(motion.h2)`
    font-family:"TMC";
    font-size:45px;
    color:${Colors.title};
    margin-left:10%;
    @media (min-width:768px){
        font-size:60px;
    }
    @media (min-width: 1200px){
        font-size:100px;
        font-weight:700;
        text-shadow: 0px 10px 20px #0D0D0D;
    }
`;
export const Title2 = styled(Title1)`
    color:${Colors.main}
`;
export const ButtonContact = styled(motion.div)`
    width:70%;
    height:40%;
    border: 2px solid ${Colors.main};
    border-radius: 10px;
    position: relative;
    top:20%;
    cursor: pointer;
    &:hover{
        background-color:${Colors.main}
    }
    p{
        font-family:"TMC";
        font-size:30px;
        color:${Colors.title};
        text-align:center;
        position:relative;
        top:5%;
    }
    @media (min-width:768px){
        width:50%;
        height:30%;
        p{
            font-size:35px;
            top:15%;
        }
    }
    @media (min-width:1200px){
        top:25%;
        width:40%;
        p{
            top:5%;
        }
    }
`;
export const MainText = styled.div`
    display:flex;
    width:70%;
    height:90%;
    align-items:center;
    justify-content:space-around;
    @media (min-width:768px){
        width:50%;
    }
    @media (min-width:1200px){
        width:100%;
        justify-content:flex-start;
        position:relative;
        right:10%;

    }
`;