import React, { useRef, useState, useEffect } from "react";
import {useInViewport} from "./useInViewport";
import {
    SectionAbout, ContainerAbout,
    TitleAbout, IMGAbout,
    TextAbout, Skills, ContainerSkills, TextSkills
    , IMGSkill, IMGSkill2
} from "../styles/AboutMeStyles.js"
import ImgAbout from "../img/Carlitos.png";
import {
    fadeInContainerWithStagger,
    fadeInDown,
    fadeinLeft
} from "../styles/Animations.js"
import webDesign from "../img/webDesign.svg"
import webDeveloper from "../img/webDeveloper.svg"
const AboutMe = () => {
    const AboutRef = useRef(null);
    const inViewport = useInViewport(AboutRef);
    return (
        <SectionAbout ref={AboutRef}>
            {inViewport &&
                <ContainerAbout
                    variants={fadeInContainerWithStagger}
                    initial="hidden"
                    animate="visible">
                    <TitleAbout
                        variants={fadeInDown}>
                        About Me
                </TitleAbout>
                    <IMGAbout
                        variants={fadeinLeft}>
                        <img src={ImgAbout} />
                    </IMGAbout>
                    <TextAbout
                        initial={{opacity:0,y:"150px"}}
                        animate={{opacity:1,y:"0"}}
                        transition={{duration:1,delay:2}}>
                        <span
                        >
                            Hello!
                    </span>
                        <p>
                           
My name is Carlos Jarero, I'm a Mexican web developer, looking to have a work experience and create a more colorful world with interface design.

                    </p>
                    </TextAbout>
                    <Skills
                    initial={{opacity:0,y:"150px"}}
                    animate={{opacity:1,y:"0"}}
                    transition={{duration:1,delay:3}}>
                        <ContainerSkills>
                            <TextSkills>
                                <h2>Web Design</h2>
                                <p>
                                    Interface development using
                                    wireframes  and pre-development designs
                        </p>
                            </TextSkills>
                            <IMGSkill src={webDesign} />
                        </ContainerSkills>
                        <ContainerSkills>
                            <TextSkills>
                                <h2>Web Depelover</h2>
                                <p>
                                    Development with html,
                                    css , javascript and React,  applying
                                    animation libraries
                        </p>
                            </TextSkills>
                            <IMGSkill2 src={webDeveloper} />
                        </ContainerSkills>
                    </Skills>
                </ContainerAbout>
            }
        </SectionAbout>)
}
export default AboutMe;